.board-list{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    @include sizeCtl(525,216);
    .board-item{
        position: absolute;
        top: 0;
        left: 0;
        @include sizeCtl(525,216);
        .board{
            @include sizeCtl(525,216);
        }
        .svg-pipe{
            overflow: visible;
        }
        &.board-item-01{
            @include itemLayout(0,1424);
            .board-name{
                .board-text{
                    display: block;
                }
                .board-bg{
                    background-color:$color-board-green;
                    .skew-block{
                        display: block;
                    }
                }
            }
            .svg-pipe{
                position: absolute;
                @include sizeCtl(188,323);
                @include itemLayout(77,58);
                z-index: 12;
            }
            .item{
                &.base{
                    @include sizeCtl(525,216);
                    top: 0;
                    left: 0;
                    z-index: 1;
                }
                &.wall{
                    @include sizeCtl(179,116);
                    top: 18px;
                    left: 7px;
                    z-index: 11;
                }

                &.intel{
                    @include sizeCtl(206,87);
                    top: 24px;
                    right: 12px;
                    z-index: 2;
                }

                &.desk{
                    @include sizeCtl(62,48);
                    top: 30px;
                    left: 77px;
                    z-index: 2;
                }

                &.led{
                    @include sizeCtl(26,109);
                    top: 0;
                    left: 11px;
                    z-index: 2;
                }
            }
        }
