/*スプライトアニメーション*/
.pechiko {
  position: absolute;
  color: red;
  width: 65px;
  height: 60px;
  background-image: url("../images/man-bike.png"); }
  .pechiko.bike1 {
    top: -100px;
    animation: sprite1 0.5s steps(7, start) infinite; }
  .pechiko.bike2 {
    top: -50px;
    animation: sprite2 0.5s steps(7, start) infinite; }
  .pechiko.bike3 {
    top: -100px;
    left: 100px;
    animation: sprite3 0.5s steps(7, start) infinite; }
  .pechiko.bike4 {
    top: -50px;
    left: -100px;
    animation: sprite4 0.5s steps(7, start) infinite; }

@keyframes sprite1 {
  0% {
    background-position: 0px 420px; }
  100% {
    background-position: 0px 0px; } }

@keyframes sprite2 {
  0% {
    background-position: 64px 420px; }
  100% {
    background-position: 64px 0px; } }

@keyframes sprite3 {
  0% {
    background-position: 180px 420px; }
  100% {
    background-position: 180px 0px; } }

@keyframes sprite4 {
  0% {
    background-position: 360px 420px; }
  100% {
    background-position: 360px 0px; } }

/*棒人間スプライトアニメーション*/
.ningen-list {
  position: absolute;
  top: 0;
  left: 160px;
  z-index: 10; }
  .ningen-list .ningen-item {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20; }

.ningen {
  position: absolute;
  width: 60px;
  height: 60px;
  background-image: url("../images/man-normal.png");
  overflow: hidden; }
  .ningen.drop1 {
    background-position: 0 0;
    animation: drop-sprite1 0.5s steps(3) infinite; }
  .ningen.drop2 {
    background-position: 180px 0;
    animation: drop-sprite2 2s steps(13) forwards; }
  .ningen.walk1 {
    background-position: 360px 0;
    animation: walk-sprite1 0.5s steps(7) infinite; }
  .ningen.walk2 {
    background-position: 540px 0;
    animation: walk-sprite2 0.5s steps(7) infinite; }

@keyframes drop-sprite1 {
  100% {
    background-position: 0 0; }
  0% {
    background-position: 0 -180px; } }

@keyframes drop-sprite2 {
  0% {
    background-position: 180px 0; }
  100% {
    background-position: 180px -780px; } }

@keyframes walk-sprite1 {
  100% {
    background-position: 360px 0; }
  0% {
    background-position: 360px -420px; } }

@keyframes walk-sprite2 {
  100% {
    background-position: 540px 0; }
  0% {
    background-position: 540px -420px; } }
