
.field{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    @include sizeCtl(1621,1038);
    z-index: 4;
}

.ground{
    position: absolute;
    top: 0;
    @include sizeCtl(1621,1038);
    z-index: 9;
}

.field-gnd{
    position: absolute;
    left: 0;
    &.front{
        top: 0;
        @include sizeCtl(1621,1038);
        z-index: 10;
    }
    &.side{
        z-index: 9;
        @include sizeCtl(1078,642);
//        @include itemLayout(272,759 / 2);
        top: 292px;
        left: 134px;
    }
}

.field{
    .field-list{
        position: relative;
        @include sizeCtl(1621,1038);
        z-index: 10;
        overflow: hidden;
        .field-item{
            position: absolute;
            z-index: 2;
            &.river {
                @include itemLayout(340,172);
                .item01{
                    @include sizeCtl(920,472);
                }
            }
            &.wood01{
                @include itemLayout(384,1134);
                .item01{
                    @include sizeCtl(96,95);
                }
            }
            &.wood02{
                @include itemLayout(750,810);
                .item01{
                    @include sizeCtl(109,134);
                }
            }
            &.building01{
                @include itemLayout(430,990);
                .item01{
                    @include sizeCtl(145,225);
                }
            }
            &.rail{
                @include itemLayout(128,982);
                position: relative;
                .item{
                    @include sizeCtl(90,58);
                    position: absolute;
                    @for $i from 1 through 16 {
                        &.item#{$i} {
                            @include itemLayout(32 * ($i - 1) ,-52 * ($i - 1))
                        }
                    }
                }

            }
        }
    }
}
